import * as F from "core/assets/fonts"
import {ImagesMetaData} from "core/types"
import * as _ from 'lodash'

export module Translations {
  export const title = "title"
}

export module Sounds {
  export const arrow = "arrow"
  export const perevorot = "perevorot"
  export const bg = "bg"
  export const letter = "letter"
  export const povorot = "povorot"
  export const visip = "visip"
  export const keys = [
    arrow,
    perevorot,
    bg,
    letter,
    povorot,
    visip,
  ]
}

export module Fonts {
  export module Colors {
    export const wrong = "#B22109"
    export const correct = "#7E42DC"
    export const correct2 = "#0989C9"
    export const normal = "#682321"
    export const gray = "#333333"
  }
  export const letter = {
    fontSize: 180, font: F.CoreFonts.MontserratSemiBold, fill: Colors.normal
  }
}
export module Images {
  export const bg = "bg"
  export const arrow = "arrow"
  export const circle = "circle"
  export const cube = (id: number, size: number) => `cube_${size}_${id}`
  export const cubeList = (size: number) => _.range(11).map(i => cube(i, size))
  export const data: ImagesMetaData = {
    sprites: []
  }

}




