import * as mazeGenerator from "@sbj42/maze-generator"
import * as originalGenerator from "@sbj42/maze-generator-backtrack"
import * as core from "@sbj42/maze-generator-core"
import {Numbers} from "core/utils/numbers"
import {RandomGenerator} from "core/utils/randomGenerator"
import {tuple} from "core/utils/tuples"
import {Utl} from "core/utils/utl"
import {UnicursalGenerator} from "games/utils/unicursalGenerator"
import * as _ from 'lodash'

export module MazeUtl {
  type Position = [number, number]
  const dirs = core.directions

  export function createLabyrinthPath(w: number, h: number) {
    const maze = mazeGenerator.generate(w, h, {generator: UnicursalGenerator.generator, originalGenerator})

    function getDirs([x, y]: Position) {
      return dirs.ALL.filter((dir: any) => maze.getPassage(x, y, dir))
    }

    const startPos = tuple(0, 0)
    const path: Position[] = []

    function generatePath([x, y]: Position, dir: any) {
      path.push([x, y])
      const newPos = dirs.move(x, y, dir)
      const newDir = getDirs(newPos).find((x: any) => x != dirs.opposite(dir))
      if (!_.isEqual(newPos, startPos)) {
        generatePath(newPos, newDir)
      }
    }

    generatePath(startPos, _.sample(getDirs(startPos)))
    return path

  }

  export function simplePath(rng: RandomGenerator, w: number, h: number) {
    const horizontal = (reverse: boolean) => _.flatMap(_.range(h), y => {
      const [right, left] = Utl.swap([[0, w], [w - 1, -1]], reverse)
      let [start, end] = Numbers.odd(y) ? right : left
      return _.range(start, end).map(x => tuple(x, y))
    })
    const vertical = (reverse: boolean) => _.flatMap(_.range(w), x => {
      const [right, left] = Utl.swap([[0, h], [h - 1, -1]], reverse)
      let [start, end] = Numbers.odd(x) ? right : left
      return _.range(start, end).map(y => tuple(x, y))
    })
    const generators = [() => vertical(true), () => vertical(false)]
    const path = rng.sample(generators)()
    return rng.bool() ? path : _.reverse(path)
  }
}




