import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {TutorialTextComponent} from "core/ui/components/games/gameUI/tutorialTextComponent"
import {OnInit} from "core/ui/components/onInit"
import {IntroComplexAnimationBehavior} from "core/ui/controls/behaviors/introComplexAnimationBehavior"
import {Img} from "core/ui/controls/img"
import {screenSizeService} from "core/ui/services/instances"
import {RandomGenerator} from "core/utils/randomGenerator"
import {Images, Sounds, Translations} from "games/polyglot/wordsearch/assets"
import {FieldComponent} from "games/polyglot/wordsearch/components/fieldComponent"
import {Task} from '../logic'

const durations = {
  arrow: 1000
}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    circle: [270, 70],
    fieldOffset: {y: -20}
  })

  positionsPortrait = Sizes.expand(this.positions, {})
  getPositions = () => screenSizeService.isPortrait ? this.positionsPortrait : this.positions
  private arrow: Img
  rng = new RandomGenerator()

  onInit(): void {
    this.statusOptions.waitCompleteBeforeNewTask = true

    this.addImage([Images.bg, [0, 0]])
    this.addImage([Images.circle, this.positions.circle]).anchorCenter()
    this.arrow = this.addImage([Images.arrow, this.positions.circle]).anchorCenter()

    this.add(this.createComponent(FieldComponent)).anchorCenter()
      .updatePos(this.center()[0], this.center()[1] + this.positions.fieldOffset.y)

    this.startIntro()
    if (this.isTutorial) {
      const tutorial = this.add(this.createComponent(TutorialTextComponent)).make().setProps(this.translate(Translations.title))
      tutorial.visible = false
      this.context.uiService.mainComponent.onIntroComplete.addOnce(() => CommonAnimations.setVisible(tutorial, true))
    }
    this.playBackgroundLoop()

    // const hint = this.add(this.createComponent(HintComponent)).defaultPosition()
    // hint.visible = false
    // if (!this.isTutorial) {
    //   this.services.uiService.mainComponent.onIntroComplete.addOnce(() => hint.visible = true)
    // }
    // this.services.uiService.hintComponent.onHint.addOnce(() => {
    //   this.playerAnswer("shuffle")
    // })
  }

  introAnimation() {
    const behavior = new IntroComplexAnimationBehavior()
    this.addBehavior(behavior)
    return behavior
  }

  onAnswer(answer: ILastAnswer) {
    if (answer.status == AnswerStatus.Correct) {
      this.animationArrow(1)
    }
  }

  animationArrow(rounds: number) {
    this.playSound(Sounds.arrow)
    this.arrow.angle = 20
    Tweens.start({
      to: {angle: this.arrow.angle + 360 * rounds},
      target: this.arrow,
      duration: durations.arrow * rounds,
      easing: Phaser.Easing.Cubic.Out
    })

  }

  onNewTask(task: Task) {
    this.onIntroCompleteOrMemorize.addOnce(() => {
      if (this.isNewLevel) {
        this.animationArrow(2)
      }
    })
  }

}



