import {Tweens} from "core/ui/animations/tweens"
import {Behavior} from "core/ui/controls/behaviors/behavior"
import {Control} from "core/ui/controls/control"
import * as _ from 'lodash'

const durations = {
  tween: 300
}

export type Shift = {
  x: number
  y: number
  angle: number
}

export class PositionShiftBehavior extends Behavior<Control> {

  currentShift: Shift = {
    x: 0,
    y: 0,
    angle: 0,
  }

  disabled = false

  constructor(private max: number, private maxAngle: number) {
    super()
  }

  resetPosition() {
    this.control.x -= this.currentShift.x
    this.control.y -= this.currentShift.y
    this.control.angle -= this.currentShift.angle
    this.currentShift = {x: 0, y: 0, angle: 0}
  }

  shift(newShift: Shift, animation: boolean) {
    if (this.disabled) {
      return
    }
    const newX = this.control.x - this.currentShift.x + newShift.x
    const newY = this.control.y - this.currentShift.y + newShift.y
    const newAngle = this.control.angle - this.currentShift.angle + newShift.angle
    if (animation) {
      Tweens.start({
        target: this.control,
        noStopCurrentTweens: true,
        to: {
          x: newX,
          y: newY,
          angle: newAngle
        },
        duration: durations.tween,
        easing: Phaser.Easing.Sinusoidal.Out
      })
    } else {
      this.control.x = newX
      this.control.y = newY
      this.control.angle = newAngle
    }
    this.currentShift = newShift
  }

  randomShift(animation: boolean) {
    const randomPosition = () => _.random(-this.max, this.max)
    const newShift = {
      x: randomPosition(),
      y: randomPosition(),
      angle: _.random(-this.maxAngle, this.maxAngle)
    }
    this.shift(newShift, animation)
  }

  onInit() {
  }
}




