import {createLevelsFromArray} from "core/types/logic"

export interface Level {
  w: number
  h: number
}

function level(w: number, h: number): Level {
  return {w, h}
}

export const levels = createLevelsFromArray([
  level(4, 3),
  level(4, 4),
  level(5, 4),
  level(5, 5),
  level(6, 5),
  level(7, 5),
  level(8, 5),
  level(9, 5),
])



