import {Sys} from "core/logic/sys"
import {AnswerStatus, ILastAnswer} from "core/types/logic"
import {CommonAnimations} from "core/ui/animations/commonAnimations"
import {Signals} from "core/ui/animations/signals"
import {Tweens} from "core/ui/animations/tweens"
import {Sizes} from "core/ui/common/sizes"
import {GameComponent} from "core/ui/components/index"
import {OnInit} from "core/ui/components/onInit"
import {PositionShiftBehavior, Shift} from "core/ui/controls/behaviors/positionShiftBehavior"
import {Btn, ImageBtn} from "core/ui/controls/btn"
import {ControlUtl} from "core/ui/controls/controlUtl"
import {screenSizeService} from "core/ui/services/instances"
import {Matrixs} from "core/utils/matrixs"
import {RandomGenerator} from "core/utils/randomGenerator"
import {tuple} from "core/utils/tuples"
import {Utl} from "core/utils/utl"
import {Fonts, Images, Sounds} from "games/polyglot/wordsearch/assets"
import {Task} from "games/polyglot/wordsearch/logic"
import * as _ from 'lodash'
import Colors = Fonts.Colors

const durations = {
  delay: 100,
  answerDelay: 1000,
  move: 500,
  hide: 700,
  rotate: 50
}

const simpleAnimation = !Sys.isDesktop
const bigLetter = ["ё", "й"]

export class FieldComponent extends GameComponent implements OnInit {
  positions = Sizes.create({
    margin: {x: 320, y: 360},
    text: {y: 141},
    textBig: {y: 154},
    answerMargin: -45,
    border: 100,
    cubeSize: 300,
    shift: 10,
    start: {x: -1000, y: -1000}
  })

  private buttons: ImageBtn[][] = []
  rng = new RandomGenerator()
  prevShifts: Shift[][] = []

  getMinSize(task: Task) {
    const size = this.getSize()
    const height = size.heigth / task.field.length
    const width = size.width / task.field[0].length
    return Math.min(height, width)
  }

  getSize() {
    return {
      width: screenSizeService.realWidth - this.positions.border,
      heigth: screenSizeService.realHeight - this.positions.border
    }
  }

  updateButtonPositions(cubeSize: { size: number, scale: number }) {
    this.onNewTask(this.getCurrentTask())
    // Matrixs.map(this.buttons, (btn, x, y) => {
    //   this.setButtonPosition(btn, [x, y], cubeSize)
    // })
    // this.updatePivot()
    // const size = this.getSize()
    // ControlUtl.scaleToFit(this, size.width, size.heigth)

  }

  setButtonPosition(btn: Btn, p: [number, number], cubeSize: { size: number, scale: number }) {
    const [xi, yi] = Utl.swap(p, screenSizeService.isPortrait)
    btn.updatePos(this.positions.margin.x * cubeSize.scale * xi, this.positions.margin.y * cubeSize.scale * yi)

  }

  getCubeSize(task: Task) {
    const minSize = this.getMinSize(task)
    return _.sortBy(_.range(3).map(size => {
      const scale = 1 - 0.25 * size
      const imgSize = this.positions.cubeSize * scale
      const diff = Math.abs(imgSize - minSize)
      return tuple({size, scale}, diff)
    }), ([_, diff]) => diff)[0][0]

  }

  newButtonAnimation(btn: Btn, simpleAnimation: boolean) {
    return simpleAnimation
      ? CommonAnimations.show(btn, true)
      : Tweens.start({
        target: btn,
        duration: durations.move,
        to: this.positions.start,
        noStopCurrentTweens: true,
        easing: Phaser.Easing.Cubic.Out,
        reverse: true,
        noAutoReset: true,
        onComplete: () => {
          btn.getBehavior(PositionShiftBehavior).disabled = false
        }
      })

  }

  checkButtons(task: Task) {
    const field = task.field
    const [buttons, letters, both] = Matrixs.intersection(this.buttons, task.field)
    const cubeSize = this.getCubeSize(task)

    buttons.forEach(([x, p]) => {
      Matrixs.set(this.buttons, p, undefined)
      this.removeChildAndDestroy(x)
    })

    const newButtons = letters.map(([x, p]) => {
      const btn = this.createButton(x, p, cubeSize)
      const [xi, yi] = Utl.swap(p, screenSizeService.isPortrait)
      btn.updatePos(this.positions.margin.x * cubeSize.scale * xi, this.positions.margin.y * cubeSize.scale * yi)
      const shift = btn.getBehavior(PositionShiftBehavior)
      shift.randomShift(false)
      shift.disabled = true
      Matrixs.set(this.buttons, p, btn)
      this.addChild(btn)
      btn.imgControl.img.animations.add("rotate",
        Images.cubeList(cubeSize.size).map(x => this.gameKey(x)), durations.rotate)
      return btn
    })

    this.updatePivot()
    const size = this.getSize()
    ControlUtl.scaleToFit(this, size.width, size.heigth)

    Signals.waitAll(
      newButtons.map(btn => this.newButtonAnimation(btn, simpleAnimation).onComplete)
    ).addOnce(() => {
      {
        this.answersAreReady()
      }
    })

    if (!simpleAnimation) {
      if (newButtons.length > 0) {
        this.playSound(Sounds.visip)
      }
    }

    both.forEach(([b, l, p]) => {
      const newText = l.toUpperCase()
      b.textControl.fill = Colors.normal
      if (b.text != newText || task.newField) {
        if (simpleAnimation) {
          b.text = newText
          CommonAnimations.show(b.textControl, true, durations.hide)
        } else {
          b.text = ""
          b.imgControl.img.animations.play("rotate").onComplete.addOnce(() => {
            b.text = newText
          })
        }
      }
    })
    if (both.length > 0) {
      this.playSound(Sounds.perevorot)
    }
    this.buttons = Matrixs.compact(this.buttons)
  }

  checkSignal = new Phaser.Signal()

  createButton(l: string, answer: [number, number], cubeSize: { scale: number, size: number }) {
    const pointerLocation = screenSizeService.isPortrait ? "bottom" : "right"
    const btn = this.addAnswerBehavior(new ImageBtn(l.toUpperCase(),
      this.gameKey(Images.cube(0, cubeSize.size)), () => {
        this.playerAnswer(answer)
        if (this.isTutorial) {
          this.playerAnswer("check")
        } else {
          this.checkSignal.dispose()
          this.checkSignal = this.delay(durations.answerDelay)
          this.checkSignal.addOnce(() => {
            this.playerAnswer("check")
          })

        }
      }, {...Fonts.letter, fontSize: Fonts.letter.fontSize * cubeSize.scale}),
      answer, {
        location: pointerLocation,
        margin: this.positions.answerMargin
      }, true).updateFields({tag: answer.toString()}).anchorCenter()
    btn.textControl.y = (_.includes(bigLetter, l) ? this.positions.textBig.y : this.positions.text.y) * cubeSize.scale
    const shift = new PositionShiftBehavior(this.positions.shift, 0)
    btn.addBehavior(shift)
    btn.onHover(out => {
      if (!out) {
        shift.randomShift(true)
        this.playSound(Sounds.povorot)
      }
    })
    btn.updatePivot()
    return btn
  }

  onNewTask(task: Task): void {
    this.onIntroCompleteOrMemorize.addOnce(() => {
      if (this.isNewLevel) {
        this.buttons = []
        this.removeAll(true)
      }
      this.checkButtons(task)
    })
  }

  onInit(): void {
    this.onChangeFor(() => screenSizeService.isPortrait,
      () => this.updateButtonPositions(this.getCubeSize(this.getCurrentTask())))
  }

  onAnswer(answer: ILastAnswer) {
    const buttonList = _.flatten(this.buttons)
    const button = buttonList.find(x => x.tag == answer.answerId.toString())!
    if (!button) {
      return
    }
    if (answer.status == AnswerStatus.Error) {
      const task = answer.levelTask.task as Task
      buttonList.forEach(x => x.textControl.fill = Colors.normal)
      const correctAnswer = task.createdAnswers.find(x => _.some(x, x => _.isEqual(x, answer.answerId)))
        || task.createdAnswers[0]
      correctAnswer.forEach(([x, y]) => {
        this.buttons[y][x].textControl.fill = Colors.correct2
      })
      button.textControl.fill = Colors.wrong
    } else if (AnswerStatus.isCorrect(answer.status)) {
      button.textControl.fill = button.textControl.fill == Colors.correct ? Colors.normal : Colors.correct
      this.playSound(Sounds.letter)
    }
  }

  onPropsUpdate() {
  }
}




