import {GameLogic, ITask} from "core/logic"
import {AnswerChecker} from "core/logic/answers"
import {Sys} from "core/logic/sys"
import {AnswerStatus} from "core/types/logic"
import {RandomGenerator} from "core/utils"
import {Arrays} from "core/utils/arrays"
import {UniqSeq} from "core/utils/circleSeq"
import {Numbers} from "core/utils/numbers"
import {tuple} from "core/utils/tuples"
import {Undef} from "core/utils/undef"
import {Words} from "games/polyglot/wordsearch/words"
import {MazeUtl} from "games/utils/mazeUtl"
import * as _ from "lodash"
import {Level} from "./levels"
import WordListWithLength = Words.WordListWithLength

const letters = "абвгдеёжзиёкламнопрстуфхцчшщъыьэюя".split("")
const goodLetters = "оаеинтрслвкпмудя".split("")

type WordPositions = Position[]

export interface Task extends ITask {
  field: string[][]
  createdAnswers: WordPositions[]
  newField: boolean
  answers: WordSearchAnswer
}

type Position = [number, number]

export class WordSearchAnswer implements AnswerChecker {
  prevAnswers: Position[] = []
  currentWord = ""
  _isCompleted = false
  _isChecked = false

  get restAnswers() {
    return [this.tutorialAnswers[this.prevAnswers.length]]
  }

  constructor(private isTutorial: boolean, private field: string[][],
              private tutorialAnswers: Position[], private allWords: string[]) {
  }

  check(answerId: Position | "check") {

    if (answerId == "check") {
      this._isChecked = true
      this.checkIsCompleted()
      return true
    }
    this._isChecked = false
    this._isCompleted = false
    const [x, y] = answerId
    const prevAnswer = _.last(this.prevAnswers)
    const distance = prevAnswer ? Math.abs(prevAnswer[0] - x) + (Math.abs(prevAnswer[1] - y)) : -1
    if (distance == 0) {
      this.currentWord = this.currentWord.slice(0, -1)
      this.prevAnswers.pop()
      return true
    }
    if (prevAnswer && ((distance != 1) || _.some(this.prevAnswers, p => _.isEqual(p, [x, y])))) {
      return "none"
    }
    if (this.isTutorial && !_.isEqual([x, y], this.restAnswers[0])) {
      return false
    }

    const answer = this.field[y][x]
    this.currentWord = this.currentWord + answer
    if (this.checkIsCompleted()) {
      this.prevAnswers.push([x, y])
      return true
    }
    return false
  }

  checkIsCompleted() {
    const [min, max] = Arrays.searchByPrefix(this.allWords, this.currentWord)
    if (min >= 0) {
      this._isCompleted = this.allWords[min] == this.currentWord
      return true
    }
    return false
  }

  get isCompleted() {
    return this._isCompleted && this._isChecked
  }
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()
  wordSeq = new UniqSeq(1)

  createPath(level: Level, shiftLength: number) {
    if (Math.min(level.w, level.h) < 4) {
      return MazeUtl.simplePath(this.rng, level.w, level.h)
    } else {
      const path = MazeUtl.createLabyrinthPath(level.w, level.h)
      return Arrays.shift(path, shiftLength)
    }
  }

  updateField(field: string[][], word: string, path: Position[]) {
    const letters = word.split("")
    path.forEach(([x, y], i) => {
      field[y][x] = letters[i]
    })
  }

  addWords(words: string[], length: number, allWords: Words.WordListWithLength) {
    if (length < allWords.minLength) {
      return
    }
    const word = length <= allWords.maxLength
      ? this.rng.sample(allWords.byLength[length]!)
      : this.wordSeq.nextFrom(allWords.words)
    words.push(word)
    this.addWords(words, length - word.length, allWords)
  }

  randomLetters(length: number, exclude: string[] = []) {
    return this.rng.subset(_.difference(goodLetters, exclude), length).join("")
  }

  newTask(level: Level, allWords: WordListWithLength) {
    const field = _.range(level.h).map(x => _.times(level.w, () => ""))
    const path = this.createPath(level, Numbers.count(allWords.minLength, 2))
    const words: string[] = []
    this.addWords(words, path.length, allWords)
    if (Sys.showLog) {
      console.log(words)
    }
    let restPath = path
    const answers: Position[][] = []
    words.forEach(word => {
      const [wordPath, rest] = Arrays.split(restPath, word.length)
      restPath = rest
      const path = this.rng.bool() ? wordPath : _.reverse(wordPath)
      answers.push(wordPath)
      this.updateField(field, word, wordPath)
    })
    this.fillEmpty(field)
    return tuple(field, answers)
  }

  fillEmpty(field: string[][]) {
    _.range(field.length).forEach(i => field[i] = field[i].map(x => x ? x : this.rng.sample(goodLetters)))
  }

  updateTask([prevTask, _]: [Task, Position[]]) {
    const answers = prevTask.answers.prevAnswers
    const restAnswers = prevTask.createdAnswers.filter(x => !Arrays.isIntercepted(x, answers))
    if (restAnswers.length == 0) {
      return undefined
    }
    const field = prevTask.field
    this.updateField(field, this.randomLetters(answers.length, answers.map(([x, y]) => field[y][x])), answers)
    return tuple(field, restAnswers)
  }

  nextTask(level: Level) {

    const minSize = Numbers.count(level.w + level.h, 2)
    const maxWordLength = Numbers.between(minSize, Words.minLength, Words.maxLength)
    const allWords = Words.getWords(maxWordLength)
    const prevTaskAnswers = this.getPrevTaskAnswers()
    const lastAnswerStatus = Undef.map(this.lastAnswer(), x => x.status)
    const newField = this.taskCreatorState.isNewLevel || lastAnswerStatus == AnswerStatus.Error
    const [field, createdAnswers] = newField
      ? this.newTask(level, allWords)
      : (this.updateTask(prevTaskAnswers!) || this.newTask(level, allWords))

    const answers = new WordSearchAnswer(!!this.gameData.settings.isTutorial, field, createdAnswers[0], Words.allWords)

    return {
      field,
      createdAnswers,
      newField,
      answers
    }
  }

}





