import * as mazegen from "@sbj42/maze-generator"
import * as core from "@sbj42/maze-generator-core"

export module UnicursalGenerator {
  const dirs = core.directions

  function randomInt(options: any, max: any) {
    return Math.floor(options.random() * max)
  }

  export function generator(maze: any, options: any) {
    options.generator = options.originalGenerator

    let originalMaze = mazegen.generate(Math.floor(maze.width() / 2), Math.floor(maze.height() / 2), options)

    function setPassage(x: any, y: any, dir: any, closed: any) {
      try {
        return maze.setPassage(x, y, dir, closed)
      } catch (e) {
      }
    }

    for (let y = 0; y < originalMaze.height(); y++) {
      for (let x = 0; x < originalMaze.width(); x++) {
        let cell = originalMaze.cell(x, y)

        setPassage(x * 2, y * 2, dirs.NORTH, cell.north())
        setPassage((x * 2) + 1, y * 2, dirs.NORTH, cell.north())
        setPassage((x * 2) + 1, y * 2, dirs.EAST, cell.east())
        setPassage((x * 2) + 1, (y * 2) + 1, dirs.EAST, cell.east())
        setPassage((x * 2), (y * 2) + 1, dirs.SOUTH, cell.south())
        setPassage((x * 2) + 1, (y * 2) + 1, dirs.SOUTH, cell.south())
        setPassage(x * 2, y * 2, dirs.WEST, cell.west())
        setPassage(x * 2, (y * 2) + 1, dirs.WEST, cell.west())

        setPassage(x * 2, y * 2, dirs.EAST, !cell.north())
        setPassage((x * 2) + 1, y * 2, dirs.SOUTH, !cell.east())
        setPassage(x * 2, (y * 2) + 1, dirs.EAST, !cell.south())
        setPassage(x * 2, y * 2, dirs.SOUTH, !cell.west())
      }
    }
  }

}



